import React from 'react';
import Carousel from 'react-bootstrap/Carousel';
import 'bootstrap/dist/css/bootstrap.min.css';
import ban1 from '../images/ban1.jpg';
import ban2 from '../images/ban2.jpg';
import ban3 from '../images/ban3.jpg';
import ban4 from '../images/ban4.jpg';
import ban5 from '../images/ban5.jpg';
import '../index.css';

const crou = () => {
    return (
        <div class="container-lg-1 container-sm-1">
  <Carousel variant="light">
  <Carousel.Item>
    <img
      className="d-block w-100"
      src={ban1}
      alt="iacsd students"
    />
  </Carousel.Item>
  <Carousel.Item>
    <img
      className="d-block w-100"
      src={ban2}
      alt="iacsd students"
    />
  </Carousel.Item>
  <Carousel.Item>
    <img
      className="d-block w-100"
      src={ban3}
      alt="iacsd students"
    />
   
  </Carousel.Item>

  <Carousel.Item>
    <img
      className="d-block w-100"
      src={ban4}
      alt="iacsd students"
    />
   
  </Carousel.Item>

  <Carousel.Item>
    <img
      className="d-block w-100"
      src={ban5}
      alt="iacsd students"
    />
  
  </Carousel.Item>
</Carousel> 

        </div>
    )
}

export default crou
