import axios from 'axios';

const USER_API_BASE_URL = 'http://eduportal-env.eba-uhc332kb.us-east-2.elasticbeanstalk.com/users';

class ApiService {

    fetchUsers() {
        return axios.get(USER_API_BASE_URL +'/getUsers');
    }
    
    fetchUserById(id) {
        return axios.get(USER_API_BASE_URL + '/getById/' + id);
    }

    deleteUser(id) {
        return axios.delete(USER_API_BASE_URL + '/delete/' + id);
    }

    addUser(user) {
        return axios.post(USER_API_BASE_URL + '/save', user);
    }

    authenticateUser(user) {
        return axios.post(USER_API_BASE_URL + '/authenticateUser', user);
    }

    updateUser(user,id) {
        return axios.put(USER_API_BASE_URL + '/update/' +id, user);
    }

    addContact(contact) {
        return axios.post('http://eduportal-env.eba-uhc332kb.us-east-2.elasticbeanstalk.com' + '/contact', contact);
    }

    setMeeting(meeting) {
        return axios.post('eduportal-env.eba-uhc332kb.us-east-2.elasticbeanstalk.com/faculty' + '/setMeeting', meeting);
    }
    getMeeting() {
        return axios.get('http://eduportal-env.eba-uhc332kb.us-east-2.elasticbeanstalk.com/faculty' + '/getMeeting');
    }
    getCourse(email) {
        return axios.get('http://eduportal-env.eba-uhc332kb.us-east-2.elasticbeanstalk.com/course' + '/getCourse/' +email);
    }
    EnrollCourse(course) {
        return axios.post('http://eduportal-env.eba-uhc332kb.us-east-2.elasticbeanstalk.com/course' + '/addCourse', course);
    }
    
}

export default new ApiService();