import React, { Component } from 'react'
import ApiService from '../../service/ApiService';
import {ToggleButton,ToggleButtonGroup,Row,Col,Container} from 'react-bootstrap';
import '../../index.css';
// import Register from '../images/Register.png';
import { useState, useEffect } from 'react';
import StudentNavbar from './StudentNavbar';
import swal from 'sweetalert';



class EnrollCourse extends Component{
   
    constructor(props){
        super(props);
        this.state ={
            email:'',
            fname: '',
            lname:'' ,
            course: '',
        }

        this.saveUser = this.saveUser.bind(this);
    }

    saveUser = (e) => {
        
        e.preventDefault();
        let course = {email:localStorage.getItem('STUDENT_email'),fname:localStorage.getItem('STUDENT_fname') ,lname:localStorage.getItem('STUDENT_lname'),course:this.state.course};
        ApiService.EnrollCourse(course)
            .then(resp => {
                console.log(resp.data);
                swal({
                    title: "Success!",
                    text: "Course Enrolled successfully!!!!",
                    icon: "success",
                    button: "OK!",
                  });  

             this.props.history.push('/Student');
            }).catch( err=>{
         
            swal({
            title: "Failure!",
            text: "Already Enrolled!!!!",
            icon: "error",
             button: "Try Again!",
             });             
            //  this.props.history.push('/addTestimonial');
            })
            
    }

  
    onChange = (e) =>
        this.setState({ [e.target.name]: e.target.value });

        
    render() {
        return(
            <>
            <StudentNavbar/>
            <div class="container-sm" style={{marginTop:"-400px"}}>
           
            <Row>
            
            <Col></Col>
                {/* <Col><Image src={Register}  width="100%" height="100%" rounded /></Col> */}
                <Col>
                <h2 className="text-center">Enroll for a course</h2>
            <div className="container">
               

                <form>
                {/* <div className="form-group" style={{margin:"30px"}}>
                    <label>head:</label>
                    <input type="text" placeholder="head" name={localStorage.getItem('STUDENT_fname')} className="form-control" value={this.state.head} onChange={this.onChange}/>
                </div> */}

                {/* <div className="form-group"  style={{margin:"30px"}}>
                    <label>title:</label>
                    <input type="text" placeholder="title" name="title" className="form-control" value={this.state.title} onChange={this.onChange}/>
                </div> */}

                {/* <div className="form-group"  style={{margin:"30px"}}>
                    <label>Course:</label>
                    <input placeholder="course" name="course" className="form-control" value={this.state.course} onChange={this.onChange}/>
                </div> */}

                <div class="form-check">
                 <input class="form-check-input"  type="radio"
              name="course"
              value="DAC"
              checked={this.state.course === "DAC"}
              onChange={this.onChange}/>
                <label class="form-check-label" >
                 DAC
                </label>
                </div>

                <div class="form-check">
                 <input class="form-check-input" type="radio"  name="course"
              value="DBDA"
              checked={this.state.course === "DBDA"}
              onChange={this.onChange}/>
                <label class="form-check-label">
                 DBDA
                </label>
                </div>

                <div class="form-check">
                 <input class="form-check-input" type="radio"  name="course"
              value="DITISS"
              checked={this.state.course === "DITISS"}
              onChange={this.onChange}/>
                <label class="form-check-label">
                DITISS
                </label>
                </div>


                <button className="btn btn-success" style={{margin:"30px"}}  onClick={this.saveUser} >Add</button>
               



            </form>
    </div>
    </Col>
    <Col></Col>
    </Row>
    </div>
    </>
        );
    }
}
export default EnrollCourse;