import { useState, useEffect } from 'react';
import StudentNavbar from './StudentNavbar';
import ApiService from '../../service/ApiService';
import { useHistory } from 'react-router';
import { Nav, DropdownButton,Container,Button,Row,Col} from 'react-bootstrap';
import {LinkContainer} from 'react-router-bootstrap';
import { Link } from 'react-router-dom';

const ViewCourse = () => {
  
  const history =useHistory();
  const [users, setUsers] = useState([]);
  
  useEffect(() => {
    console.log('in use effect');
    ApiService.getCourse(localStorage.getItem('STUDENT_email'))
      .then((resp) => {
        setUsers(resp.data);
        console.log('res', resp.data);
        console.log('users', users);
      })
      .catch((err) => console.error('error', err));
       console.log('in useEfect');
  }, []);

 

  return (
    <>
  <StudentNavbar/>
  <Container style={{marginLeft:"143px"}}>
    <div class="container-sm" style={{marginTop:"-400px"}}>
    <h2 className="text-center">Course Enrolled</h2>
      {
        <table className='table table-striped'>
          <tr>
            <th>Id</th>
            <th>Email</th>
            <th>First Name</th>
            <th>Last Name</th>
            <th>Course</th>
           
          </tr>
          <tbody>
            {/* {users.map((user) => ( */}
              {/* <tr key={user.email}> */}
              <tr>
                <td>{users.id}</td>
                <td>{users.email}</td>
                <td>{users.fname}</td>
                <td>{users.lname}</td>
                <td>{users.course}</td>
              </tr>
            {/* ))} */}
          </tbody>
        </table>
      }
    </div>
    </Container>
    </>
  );
};

export default ViewCourse;
