import React from 'react'
import '../index.css';
import Form from 'react-bootstrap/Form';
import {Nav,Button,Row,Col} from 'react-bootstrap';
import {LinkContainer} from 'react-router-bootstrap';
import {useHistory} from 'react-router-dom';
import { useState, useEffect } from 'react';
import ApiService from '../service/ApiService';
import swal from 'sweetalert';


const Login = () => {

    const [Email ,setEmail]=useState('')
    const [Password ,setPassword]=useState('')

    const history = useHistory();

    useEffect(( ) => {
       if(sessionStorage.getItem('User')) {
       history.push('/alumni');
     }
  },[])

    const signinUser =(e)=>{
   e.preventDefault();
        if(Email.length===0){
           // alert('Please enter email  !!!!')
            swal({
              title: "Failed!",
              text: "Please enter email !!!!!!!!",
              icon: "error",
              button: "Try again!",
            });
        }else if(Password.length===0){
           // alert('Please enter password  !!!!') 
            swal({
              title: "Failed!",
              text: "Please enter password !!!!!!!!",
              icon: "error",
              button: "Try again!",
            });
        }else {
        const data = new FormData()

        // add the data
        data.append('Email', Email)
        data.append('Password', Password)
        ApiService.authenticateUser(data).then((response) => {
            const result = response.data
            console.log(result.data);
            if (result.status === 'success') {
              //alert('successfully Logged in')
              swal({
                title: "success",
                text: "Logged in successfully !!!!!!!!",
                icon: "success",
                button: "OK!",
              });
              // sessionStorage.setItem('user-info',Email)
              //localStorage.setItem('users',result.data.name)
              if(result.data.role==="STUDENT"){
                localStorage.setItem('STUDENT_id',result.data.id)
                localStorage.setItem('STUDENT_email',result.data.email)
                localStorage.setItem('STUDENT_fname',result.data.fname)
                localStorage.setItem('STUDENT_lname',result.data.lname)
                localStorage.setItem('STUDENT_number',result.data.number)
                localStorage.setItem('STUDENT_password',result.data.password)
                localStorage.setItem('STUDENT_role',result.data.role)
                localStorage.setItem('STUDENT_active',result.data.active)
                history.push('/Student')

              } else if (result.data.role==="FACULTY") {
                localStorage.setItem('FACULTY_id',result.data.id)
                localStorage.setItem('FACULTY_email',result.data.email)
                localStorage.setItem('FACULTY_fname',result.data.fname)
                localStorage.setItem('FACULTY_lname',result.data.lname)
                localStorage.setItem('FACULTY_number',result.data.number)
                localStorage.setItem('FACULTY_password',result.data.password)
                localStorage.setItem('FACULTY_role',result.data.role)
                localStorage.setItem('FACULTY_active',result.data.active)
                history.push('/FacultyPanel')

              } else if(result.data.role==="ADMIN") {
                localStorage.setItem('ADMIN_id',result.data.id)
                localStorage.setItem('ADMIN_email',result.data.email)
                localStorage.setItem('ADMIN_fname',result.data.fname)
                localStorage.setItem('ADMIN_lname',result.data.lname)
                localStorage.setItem('ADMIN_number',result.data.number)
                localStorage.setItem('ADMIN_password',result.data.password)
                localStorage.setItem('ADMIN_role',result.data.role)
                localStorage.setItem('ADMIN_active',result.data.active)
                history.push('/admin')
              }
              //history.push('/add')
            } else {
              swal({
                title: "Failed!",
                text: "Please enter valid email or password!!!!",
                icon: "error",
                button: "Try again!",
              });
            }
          })
        }
    } 


    return (
        <div>
             <Form>
           
           <Form.Group className="mb-1" controlId="formBasicEmail">
          <Form.Label>Email address</Form.Label>
          <Form.Control type="email" value={Email} name="Email" onChange={(event)=>{setEmail(event.target.value)}} placeholder="Enter email"/>
        </Form.Group>
        <Form.Group className="mb-1" controlId="formBasicPassword">
          <Form.Label>Password</Form.Label>
          <Form.Control type="password"  name="password" value={Password} placeholder="password" onChange={(event)=>{setPassword(event.target.value)}}/>

        </Form.Group>
        <Button variant="primary" type="submit" onClick={signinUser}>
          Submit
        </Button>
      


      <LinkContainer to="/reg">
      <Nav.Link >Register</Nav.Link>
      </LinkContainer>
      
      </Form>
        </div>
    )
}

export default Login
