import React from 'react';
import {Card,CardGroup,Container,Row,Col }from 'react-bootstrap';
import dac from '../images/dac.jpg';
import hacking from '../images/hacking.jpg';
import bigdata from '../images/bigdata.jpg';
import predac from '../images/predac.jpg';
import {Link} from 'react-router-dom'
import {LinkContainer} from 'react-router-bootstrap';
import { Navbar, Nav} from 'react-bootstrap';

const About = () => {
    return (
      <>
    <Container fluid>
      <Row>
        {/* <div className="container-sm-1"  style={{Color:'black'}}> */}
      <CardGroup style={{color:'blue'}}>
      <Col xs={12} sm={12} lg={3}>

      <Link to="/reg">
     
   <Card>
    <Card.Img variant="top" src={predac} />
    <Card.Body>
      <Card.Title><p style={{textAlign:'center', color:'blue'}}>PRE-DAC</p></Card.Title>
      <Card.Text>
The Institute is conducting Pre-DAC course since its inception. The courses are run on regular as well as fast track.
      </Card.Text>
    </Card.Body>
    
  </Card>
 
  </Link>


  </Col>
 
 
    
   
      



  <Col xs={12} sm={12} lg={3}>
  <Link to="/reg">
  <Card>
    <Card.Img variant="top" src={dac} />
    <Card.Body>
      <Card.Title><p style={{textAlign:'center'}}>DAC</p></Card.Title>
      <Card.Text>
PG-DAC course is targeted towards engineers and IT professionals who wish to venture into the domain of advanced computing.
      </Card.Text>
    </Card.Body>
   
  </Card>
  </Link>
  </Col>
  <Col xs={12} sm={12} lg={3}>
  <Link to="/reg">
  <Card>
    <Card.Img variant="top" src={bigdata} />
    <Card.Body>
      <Card.Title><p style={{textAlign:'center'}}>DBDA</p></Card.Title>
      <Card.Text>
PG-DBDA course has a unique focus of technologies related to big data like Hadoop, Cloud computing.


      </Card.Text>
    </Card.Body>
    
  </Card>
  </Link>

  </Col>
  <Col xs={12} sm={12} lg={3}>
  <Link to="/reg">
  <Card>
    <Card.Img variant="top" src={hacking} />
    <Card.Body>
      <Card.Title><p style={{textAlign:'center'}}>DITISS</p></Card.Title>
      <Card.Text>
PG-DITISS course has focus on IT Infrastructure, Networking, Cyber Security, Cyber Forensics domain.
      </Card.Text>
    </Card.Body>

  </Card>
  </Link>
  </Col>
</CardGroup>     


</Row>
</Container>
</>
    )
}

export default About
