import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Nav, DropdownButton,Dropdown,Button,Row,Col} from 'react-bootstrap';
import {LinkContainer} from 'react-router-bootstrap';
import ButtonGroup from 'react-bootstrap/ButtonGroup';
import { useHistory } from 'react-router';
import swal from 'sweetalert';


const AdminNavbar = () => {
 
  
  const history = useHistory();
  const logout = () => {
        
    localStorage.removeItem('ADMIN_id')
    localStorage.removeItem('ADMIN_email')
    localStorage.removeItem('ADMIN_fname')
    localStorage.removeItem('ADMIN_lname')
    localStorage.removeItem('ADMIN_number')
    localStorage.removeItem('ADMIN_password')
    localStorage.removeItem('ADMIN_role')
    localStorage.removeItem('ADMIN_active')
    
    swal({
      title: "Success!",
      text: "successfully logged out!!!!",
      icon: "success",
      button: "OK!",
    });  

    history.push('/')
    
}
    return (
<div ClassName="leftPanel" style={{marginRight:"1500px"}}> 

<ButtonGroup vertical>

<Button> <LinkContainer to="/admin">
    <Nav.Link className="nav-links">HOME</Nav.Link>
    </LinkContainer></Button>

  <Button> <LinkContainer to="/PostPlacementRecord">
    <Nav.Link className="nav-links">PLACEMENTS</Nav.Link>
    </LinkContainer></Button>
  
    <Button> <LinkContainer to="/Users">
    <Nav.Link className="nav-links">STUDENTS</Nav.Link>
    </LinkContainer></Button>
  
    <Button> <LinkContainer to="/displayAllFaculty">
    <Nav.Link className="nav-links">FACULTIES</Nav.Link>
    </LinkContainer></Button>
   

    <Button> <LinkContainer to="/displayAllAdmin">
    <Nav.Link className="nav-links">ADMIN</Nav.Link>
    </LinkContainer></Button>



  <Button  onClick={logout} > <LinkContainer to="/">
    <Nav.Link className="nav-links">LOGOUT</Nav.Link>
    </LinkContainer></Button>
  
</ButtonGroup>

</div>
)
};
 

export default AdminNavbar
