import React, { Component } from 'react'
import ApiService from '../../service/AdminServices';
import {ToggleButton,ToggleButtonGroup,Row,Col,Container} from 'react-bootstrap';
import '../../index.css';
// import Register from '../images/Register.png';
import { useState, useEffect } from 'react';
import StudentNavbar from './StudentNavbar';
import swal from 'sweetalert';



class AddTestimonial extends Component{
   
    constructor(props){
        super(props);
        this.state ={
            head: '',
            title:'' ,
            text: '',
        }

        this.saveUser = this.saveUser.bind(this);
    }

    saveUser = (e) => {
        
        e.preventDefault();
        let testimonial = {head:localStorage.getItem('STUDENT_fname')+' ' +localStorage.getItem('STUDENT_lname'), title:this.state.title, text:this.state.text, footer:this.state.footer};
        ApiService.addTestimonials(testimonial)
            .then(resp => {
                console.log(resp.data);
                swal({
                    title: "Success!",
                    text: "Testimonial added successfully!!!!",
                    icon: "success",
                    button: "OK!",
                  });  

             this.props.history.push('/Student');
            }).catch( err=>{
         
            swal({
            title: "Failure!",
            text: "Testimonial adding Failed!!!!",
            icon: "error",
             button: "Try Again!",
             });             
             this.props.history.push('/addTestimonial');
            })
            
    }

  
    onChange = (e) =>
        this.setState({ [e.target.name]: e.target.value });

        
    render() {
        return(
            <>
            <StudentNavbar/>
           
            <div class="container-sm" style={{marginTop:"-400px"}}>
            <Row>
           
            <Col></Col>
                {/* <Col><Image src={Register}  width="100%" height="100%" rounded /></Col> */}
                <Col>
            <div className="container">
               

                <h2 className="text-center">Add Testimonials</h2>
                <form>
                {/* <div className="form-group" style={{margin:"30px"}}>
                    <label>head:</label>
                    <input type="text" placeholder="head" name={localStorage.getItem('STUDENT_fname')} className="form-control" value={this.state.head} onChange={this.onChange}/>
                </div> */}

                <div className="form-group"  style={{margin:"30px"}}>
                    <label>Comment:</label>
                    <input type="text" placeholder="Comment" name="title" className="form-control" value={this.state.title} onChange={this.onChange}/>
                </div>

                <div className="form-group"  style={{margin:"30px"}}>
                    <label>Description:</label>
                    <input placeholder="Description" name="text" className="form-control" value={this.state.text} onChange={this.onChange}/>
                </div>

            

                <button className="btn btn-success" style={{margin:"30px"}}  onClick={this.saveUser} >Add</button>
               



            </form>
    </div>
    </Col>
    <Col></Col>
    </Row>
   </div>
    {/* </Container> */}
    </>
        );
    }
}
export default AddTestimonial;