import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import Container from 'react-bootstrap/Container';
import { Navbar, Nav, NavDropdown,Button,Row,Col} from 'react-bootstrap';
import {LinkContainer} from 'react-router-bootstrap';
import Form from 'react-bootstrap/Form';
import logo from '../images/logo.png';
import '../index.css';
import Login from './Login';
import FacebookIcon from '@material-ui/icons/Facebook';
import InstagramIcon from '@material-ui/icons/Instagram';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import IconButton from '@material-ui/core/IconButton';
import YouTubeIcon from '@material-ui/icons/YouTube';
import iacsd from '../images/iacsd.jpg';
import MenuSharpIcon from '@material-ui/icons/MenuSharp';


const AppNavbar = () => {

    return (
<>
<Container d-block d-sm-none >
    {/* <div class="container-fluid" style={{backgroundColor:"whitesmoke"}}> */}
  <Row>
    <Col lg={4} xs={4} style={{alignItems:'left',marginTop:"10px"}}><img src={logo} height= "60px" alt="IACSD" /></Col>
    <Col lg={4} xs={4} style={{textAlign:'center',color:'black'}}>Dr. D. Y. Patil Pratishthan's<p>
Institute for Advanced Computing and Software Development</p></Col>
    
    <Col lg={4} xs={4} style={{textAlign:"right",marginTop:"2px"}}>
   
    <IconButton aria-label="delete" href="https://www.facebook.com/" target="_blank">
    <FacebookIcon color="primary" />
    </IconButton>

    <IconButton aria-label="delete" href="https://www.instagram.com/" target="_blank">
    <InstagramIcon color="secondary" />  
    </IconButton>

    <IconButton aria-label="delete" href="https://www.LinkedIn.com/" target="_blank">
    <LinkedInIcon color="primary" /> 
    </IconButton>
  
    <IconButton aria-label="delete" href="https://www.youtube.com/channel/UC9DyttSIMJKud0YKQLNLU0w?view_as=subscriber" target="_blank">
    <YouTubeIcon color="secondary" /> 
    </IconButton>
    
    </Col>
  </Row>
  </Container>


<Navbar  collapseOnSelect  expand="lg"   sticky="top"  variant="" >
<div className="d-block d-sm-none " style={{textAlign:'center'}}> <img src={iacsd} alt="iacsd" width='100px' /></div>
<Navbar.Brand href="#home">

</Navbar.Brand>

  <div claaName="container-xs">
  <Navbar.Toggle aria-controls="responsive-navbar-nav"  >
 <MenuSharpIcon/>
  </Navbar.Toggle>
  <Navbar.Collapse id="responsive-navbar-nav"  style={{textAlign:'center'}} >
  
    <Nav className="me-auto">
 
    
    <LinkContainer to="/">
    <Nav.Link className="nav-links">HOME</Nav.Link>
    </LinkContainer>
    
      <LinkContainer to="/courses">
    <Nav.Link className="nav-links">COURSES</Nav.Link>
    </LinkContainer>
      
    <LinkContainer to="/placements">
    <Nav.Link className="nav-links">PLACEMENTS</Nav.Link>
    </LinkContainer>
      

    <LinkContainer to="/alumni">
    <Nav.Link className="nav-links">ALUMNI</Nav.Link>
    </LinkContainer>
      

    <LinkContainer to="/testimonials">
    <Nav.Link className="nav-links">TESTIMONIALS</Nav.Link>
    </LinkContainer>
   
    <LinkContainer to="/about">
    <Nav.Link className="nav-links"> ABOUT US</Nav.Link>
    </LinkContainer>

    <LinkContainer to="/contact">
    <Nav.Link className="nav-links">CONTACT US</Nav.Link>
    </LinkContainer>  

    </Nav>
    <Nav>
    <NavDropdown  title="LOGIN" id="collasible-nav-dropdown" autoClose="outside">
        <div className="fom">
  
<Login/>
</div>
</NavDropdown> 
</Nav>
</Navbar.Collapse>
</div>
</Navbar>
</>
)
};
 

export default AppNavbar
