import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import Container from 'react-bootstrap/Container';
import { Nav, DropdownButton,Dropdown,Button,Row,Col} from 'react-bootstrap';
import {LinkContainer} from 'react-router-bootstrap';
import ButtonGroup from 'react-bootstrap/ButtonGroup';
import { useHistory } from 'react-router';
import swal from 'sweetalert';


const AdminNavbar = () => {
 
  
  const history = useHistory();
  const logout = () => {
        
    localStorage.removeItem('STUDENT_id')
    localStorage.removeItem('STUDENT_email')
    localStorage.removeItem('STUDENT_fname')
    localStorage.removeItem('STUDENT_lname')
    localStorage.removeItem('STUDENT_number')
    localStorage.removeItem('STUDENT_password')
    localStorage.removeItem('STUDENT_role')
    localStorage.removeItem('STUDENT_active')
    swal({
      title: "Success!",
      text: "successfully logged out!!!!",
      icon: "success",
      button: "OK!",
    });  

    history.push('/')
    
}
    return (
<div ClassName="leftPanel" style={{marginRight:"1500px"}}> 
{/* <Row>
  <Col> */}
<ButtonGroup vertical>

<Button> <LinkContainer to="/Student">
    <Nav.Link className="nav-links">HOME</Nav.Link>
    </LinkContainer></Button>

  <Button> <LinkContainer to="/addTestimonial">
    <Nav.Link className="nav-links">Add Testimonial</Nav.Link>
    </LinkContainer></Button>
  
    <Button> <LinkContainer to="/ViewCourse">
    <Nav.Link className="nav-links">View Course</Nav.Link>
    </LinkContainer></Button>
  
    <Button> <LinkContainer to="/ViewMeeting">
    <Nav.Link className="nav-links">View Meeting</Nav.Link>
    </LinkContainer></Button>

    <Button> <LinkContainer to="/EnrollCourse">
    <Nav.Link className="nav-links">Enroll Course</Nav.Link>
    </LinkContainer></Button>

  <Button  onClick={logout} > <LinkContainer to="/">
    <Nav.Link className="nav-links">LOGOUT</Nav.Link>
    </LinkContainer></Button>
  
</ButtonGroup>

</div>
)
};
 

export default AdminNavbar
