import React, { Component } from 'react'
import AdminService from '../../service/AdminServices';
import {Row,Col,Image} from 'react-bootstrap';
import '../../index.css';
import AdminNavbar from './AdminNavbar';
import AddPlacement from '../../images/AddPlacement.png';
import swal from 'sweetalert';

class PostPlacementRecord extends Component{


    constructor(props){
        super(props);
        this.state ={
            name: '',
            company: '',
            course: '',

        }

        this.saveUser = this.saveUser.bind(this);
    }

    saveUser = (e) => {
        
        e.preventDefault();
        let student = {name: this.state.name, company: this.state. company, course: this.state. course};
        AdminService.addPlacementRecords(student)
            .then(resp => {
                console.log(resp.data);
                swal({
                    title: "Success!",
                    text: "Placement record added successfully!!!!",
                    icon: "success",
                    button: "OK!",
                  });  

              // this.props.history.push('/Placements');
            }).catch( err=>{
                swal({
                    title: "Failure!",
                    text: "Placement record adding Failed!!!!",
                    icon: "error",
                    button: "Try Again!",
                  });           
                //this.props.history.push('/Placements');
            })
            
    }

  

    onChange = (e) =>
        this.setState({ [e.target.name]: e.target.value });

        
    render() {
        return(
            <>
           <AdminNavbar/>
            <Row>
            <Col><Image src={AddPlacement} width="150%" height="180%" rounded  style={{marginTop:"-350px"}}/></Col>
                <Col>
            <div className="container" style={{marginTop:"-250px"}}>
               

                <h2 className="text-center">Add Record</h2>
                <form>
                <div className="form-group" style={{margin:"30px"}}>
                    <label>Name:</label>
                    <input type="text" placeholder="Name" name="name" className="form-control" value={this.state.name} onChange={this.onChange}/>
                </div>

                <div className="form-group"  style={{margin:"30px"}}>
                    <label>Company:</label>
                    <input type="text" placeholder="Company" name="company" className="form-control" value={this.state.company} onChange={this.onChange}/>
                </div>

                <div className="form-group"  style={{margin:"30px"}}>
                    <label>Course:</label>
                    <input placeholder="Course" name="course" className="form-control" value={this.state.course} onChange={this.onChange}/>
                </div>

                <button className="btn btn-success" style={{margin:"30px"}}  onClick={this.saveUser} >Add</button>
               
            </form>
    </div>
    </Col>
    
    </Row>
    </>
        );
    }
}
export default PostPlacementRecord;