import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import Container from 'react-bootstrap/Container';
import { Nav, DropdownButton,Dropdown,Button,Row,Col} from 'react-bootstrap';
import {LinkContainer} from 'react-router-bootstrap';
import ButtonGroup from 'react-bootstrap/ButtonGroup';
import { useHistory } from 'react-router';
import swal from 'sweetalert';


const FacultyNavbar = () => {
 
  
  const history = useHistory();
  const logout = () => {

    localStorage.removeItem('FACULTY_id')
    localStorage.removeItem('FACULTY_email')
    localStorage.removeItem('FACULTY_fname')
    localStorage.removeItem('FACULTY_lname')
    localStorage.removeItem('FACULTY_number')
    localStorage.removeItem('FACULTY_password')
    localStorage.removeItem('FACULTY_role')
    localStorage.removeItem('FACULTY_active')

    swal({
      title: "Success!",
      text: "successfully logged out!!!!",
      icon: "success",
      button: "OK!",
    });  

    history.push('/')
    
}
    return (
<div ClassName="leftPanel" style={{marginRight:"1500px"}}> 

<ButtonGroup vertical>

<Button> <LinkContainer to="/FacultyPanel">
    <Nav.Link className="nav-links">HOME</Nav.Link>
    </LinkContainer></Button>

    <Button> <LinkContainer to="/setMetting">
    <Nav.Link className="nav-links">SET METTING</Nav.Link>
    </LinkContainer></Button>

    <Button> <LinkContainer to="/studentRecordFac">
    <Nav.Link className="nav-links">STUDENT RECORD</Nav.Link>
    </LinkContainer></Button>


  <Button  onClick={logout} > <LinkContainer to="/">
    <Nav.Link className="nav-links">LOGOUT</Nav.Link>
    </LinkContainer></Button>
  
</ButtonGroup>

</div>
)
};
 

export default FacultyNavbar
